.app {
  min-height: 100vh;
  padding: 100px;
  text-align: center;
}

.buttons {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 20px;
}

.button {
  border: 1px solid #000;
  border-radius: 5px;
  background-color: aquamarine;
  padding: 5px 10px;
  font-size: inherit;
  cursor: pointer;
}

.disabled {
  opacity: 0.3;
  cursor: default;
  transition: .1s;
}

.main-timer {
  font-size: 120px;
  margin-bottom: 20px;
}

.remains-time {
  font-size: 52px;
  margin-bottom: 20px;
}

.big-timer {
  font-size: 96px;
  color: red;
}

.list {
  list-style: none;
  text-align: left;
}

.item {
  margin-bottom: 10px;
  font-size: 30px;
}

.remaints {
  font-style: italic;
  color: blueviolet;
}
